import {Injectable} from '@angular/core';
// @ts-ignore
import {Client} from '../models/client.model';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable,} from 'rxjs';
import {concatMap, filter} from 'rxjs/operators';

//const baseUrl = 'http://192.168.0.246:8080/api/client';
const baseUrl = 'https://oafish-orange-production.up.railway.app/api/client';

@Injectable({
  providedIn: 'root',
})
export class ClientService {
  constructor(private http: HttpClient) {
  }

  getAll(): Observable<Client[]> {
    return this.http.get<Client[]>(baseUrl);
  }

  getClientId(id: number): Observable<Client> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  delete(id: any): Observable<any> {
    return this.http.delete(`${baseUrl}/${id}`);
  }
  create(data: any): Observable<any> {
    return this.http.post(baseUrl, data);
  }
  update(id: any, data: any): Observable<any> {
    return this.http.put(`${baseUrl}/${id}`, data);
  }

  findByName(title: any): Observable<Client[]> {
    return this.http.get<Client[]>(`${baseUrl}?name=${name}`);
  }
}
