import {Component, HostListener, Input, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {AuthGoogleService} from '../services/authGoogle.service';
import {TranslateService} from '@ngx-translate/core';
import {TokenStorageService} from '../_servicesAuth/token-storage.service';
import {AuthService} from '../_servicesAuth/auth.service';

import {Platform} from '@ionic/angular';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  form: any = {
    username: null,
    password: null
  };
  isLoggedIn = false;
  isLoginFailed = false;
  errorMessage = '';
  isMobile: boolean;
  startY: number;
  distance = 0;
  focused: boolean;


  private opt = 'signin';

  constructor(
    private router: Router,
    private authGoogleService: AuthGoogleService,
    private authService: AuthService,
    private translate: TranslateService,
    private tokenStorage: TokenStorageService,
    private platform: Platform
  ) {
    //translate.setDefaultLang('en');
    // Initialiser le service de traduction
    translate.setDefaultLang('fr'); // Langue par défaut
    // translate.use('fr'); // Utiliser la langue spécifiée

    this.isMobile = this.platform.is('mobile');

  }

  ngOnInit() {

    if (this.tokenStorage.getToken()) {
      this.isLoggedIn = true;

    }

    this.authGoogleService.handleGoogleCallback()
      .then(token => {
        console.log('this is the Token d\'accès /->:', token);
        // Ici, vous pouvez envoyer le token d'authentification au Backend
        this.router.navigate(['/dashboard']);

      })
      .catch(error => {
        console.error('Erreur lors de la récupération du token:', error);
      });
  }

  async signInWithGoogle() {
    this.authGoogleService.signInWithGoogle();
  }

  onLanguageChange(event) {
    this.translate.use(event.detail.value);
    //console.log(event.detail.value); // the value of the selected option
  }

  login() {
    this.router.navigate(['/dashboard']);
  }

  /* ---------------------- Login ------------------------*/

  onSubmit(): void {
    const {username, password} = this.form;

    this.authService.login(username, password).subscribe(
      data => {
        this.tokenStorage.saveToken(data.accessToken);
        this.tokenStorage.saveUser(data);

        this.isLoginFailed = false;
        this.isLoggedIn = true;
        this.router.navigate(['/admin']);
        //this.reloadPage();
      },
      err => {
        this.errorMessage = err.error.message;
        this.isLoginFailed = true;
      }
    );
  }

  reloadPage(): void {
    window.location.reload();
    this.router.navigate(['/dashboard']);

  }

  /*****************************************************/

  onBlur(event: any) {
    const value = event.target.value;

    if (!value) {
      this.focused = false;
    }
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  @HostListener('document:touchstart', ['$event'])
  onTouchStart(event) {
    this.startY = event.touches[0].clientY;
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  @HostListener('document:touchmove', ['$event'])
  onTouchMove(event) {
    const currentY = event.touches[0].clientY;
    this.distance = Math.abs(currentY - this.startY);
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  @HostListener('document:touchend', ['$event'])
  onTouchEnd(event) {
    if (this.distance >= 400) {
      // L'utilisateur a glissé d'une distance de 5 cm ou plus
      window.location.reload();
    }

    // Réinitialisez la distance
    this.distance = 0;
  }
}
