// @ts-ignore

import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import {HttpClientModule} from '@angular/common/http';
// @ts-ignore
import {NgxImageCompressService} from 'ngx-image-compress';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpClient } from '@angular/common/http';
import { Stripe } from '@ionic-native/stripe/ngx';
import {LoginModule} from './client1/login/login.module';
import {SignupModule} from './client1/signup/signup.module';
import {FormsModule} from '@angular/forms';
import {PageNotFoundModule} from './client1/page-not-found/page-not-found.module';
import { GooglePlus } from '@ionic-native/google-plus/ngx';
import {authInterceptorProviders} from './client1/_helpers/auth.interceptor';

// Fonction pour charger les fichiers de traduction
// eslint-disable-next-line @typescript-eslint/naming-convention,prefer-arrow/prefer-arrow-functions
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [AppComponent],
  entryComponents: [],
  imports: [
    BrowserModule,
    FormsModule,
    IonicModule.forRoot(),
    AppRoutingModule,
    HttpClientModule,
    /*TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (http: HttpClient) => new TranslateHttpLoader(http, 'assets/i18n/', '.json'),
        deps: [HttpClient]
      }
    }),*/

    TranslateModule.forRoot({
      defaultLanguage: 'en', // Langue par défaut
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    LoginModule,SignupModule,PageNotFoundModule
  ],
  providers: [
    authInterceptorProviders,
    NgxImageCompressService,
    Stripe,
    GooglePlus,
    { provide: RouteReuseStrategy, useClass: IonicRouteStrategy },
    ],
  bootstrap: [AppComponent],
})
export class AppModule {

}
