import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {LoginComponent} from './client1/login/login.component';
import {RegisterComponent} from './client1/register/register.component';


const routes: Routes = [

  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  {
    path: 'admin',
    loadChildren: () => import('../app/client1/admin/home-admin/home-admin.module').then((m) => m.HomeAdminPageModule),
  },
  {
    path: 'client',
    loadChildren: () => import('../app/client1/client/home/home.module').then((m) => m.HomePageModule),
  },
  {
    path: 'command',
    loadChildren: () =>
      import('../app/client1/command/home/home.module').then((m) => m.HomePageModule),
  },
  {
    path: '**',
    redirectTo: '/login',
    pathMatch: 'full'
  }
];

@NgModule({
  /*imports: [
    RouterModule.forRoot(routes, {preloadingStrategy: PreloadAllModules}),
  ],*/
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {
}
